.link {

  .control {
    top: 0;
    left: 0;

    a {
      display: block;
      width: 110px;
      height: 115px;
      overflow: hidden;
    }

    img {
      margin: -5px 0 0 -5px;
      width: 156px;
      height: 120px;
      display: block;
    }

    .bubble {
      font-family: monospace;
      color: $white;
      @include font-14;
      position: absolute;
      top: 30px;
      left: 120px;
      width: 210px;
    }
  }
}



